@import '/src/common/styles/sass/variables';
@import '/src/common/styles/sass/mixins';

.project {
	width: 450px;
	margin-top: 60px;
	@include flex(column, flex-start, stretch, nowrap);

	.image {
		height: 300px;
		@include flex(row, center, center, nowrap);
		@include bgSettings();
		position: relative;

		&::before {
			position: absolute;
			content: '';
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			opacity: 0.4;
			background-color: black;
		}
	}

	.projectInfo {
		padding: 25px 20px;
		background-color: $bgSecondary;

		.projectTitle {
			@include text($titleFontFamily, 16px, 1.2, 800);
			letter-spacing: 1px;
			margin: 0 0 5px;
		}

		.description {
			@include text($textFontFamily, 12px, 1.8, 400);
		}
	}
}